import React, { Component } from "react";
import styles from "./Contact.module.scss";

export class Contact extends Component {
  render() {
    return (
      <div className={styles.container}>
        <h1 className={styles.container__title}>Contact me:</h1>
        <form
          className={styles.form}
          action="https://mailthis.to/szczepanski"
          method="POST"
        >
          <span className={styles.form__label}>Name</span>
          <input
            className={styles.form__input}
            type="text"
            name="name"
            placeholder="Your name"
            required
          />

          <span className={styles.form__label}>Email</span>
          <input
            className={styles.form__input}
            type="email"
            name="_replyto"
            placeholder="Your email"
            required
          />

          <span className={styles.form__label}>Message</span>
          <textarea
            className={styles.form__textarea}
            name="message"
            rows="8"
            placeholder="You look great today."
            required
          />
          <input type="hidden" name="_honeypot" value="" />
          <input type="hidden" name="_confirmation" value="" />
          <input className={styles.form__submit} type="submit" value="Send" />
        </form>
      </div>
    );
  }
}

export default Contact;
